<template>
  <div class="apply">
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" />
    <van-row gutter="20">
      <van-col span="24">
        <van-cell-group>
          <van-radio-group v-model="form.category" class="radio">
            <van-radio name="1" style="padding:0 10px;"></van-radio>
            <van-radio name="2" style="padding:0 10px;"></van-radio>
          </van-radio-group>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.score" placeholder="" label="" maxlength="3" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" disabled @click="handleLocalClick" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" />
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-button type="primary" bottom-action class="btn" @click="handleConfirm"></van-button>
      </van-col>
    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      form: {}
    }
  },
  methods: {
    handleLocalClick() {},
    handleConfirm() {
      this.$dialog
        .confirm({
          title: '',
          message: ''
        })
        .then(() => {
          this.$router.push({ name: 'Report' })
        })
        .catch(() => {
          this.$toast.fail('')
        })
    }
  },
  created() {
    // document.title = ''
  }
}
</script>

<style lang="scss" scoped>
.apply {
  height: 100vh;
  .btn {
    margin-top: 20px;
  }
}

.radio {
  display: flex;
  padding: 10px;
  text-align: center;
  justify-content: space-evenly;
}
</style>


