<template>
  <div>
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" @click-right="$router.push({name: 'InputPersonInfo'})" />

    <van-row>
      <van-col span="24">
        <van-steps :active="step">
          <van-step></van-step>
          <van-step></van-step>
          <van-step></van-step>
        </van-steps>
      </van-col>
    </van-row>

    <van-row>
      <van-col span="24" class="tips">
        <br> 5
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-uploader :after-read="onRead">
          <van-cell title="" value="" />
        </van-uploader>
      </van-col>
      <van-col span="24">
        <van-uploader :after-read="onRead">
          <van-cell title="" value="" />
        </van-uploader>
      </van-col>
      <van-col span="24">
        <van-uploader :after-read="onRead">
          <van-cell title="" value="" />
        </van-uploader>
      </van-col>

    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      step: 0,
      form: {}
    }
  },
  methods: {
    onRead() {}
  }
}
</script>


<style lang="scss" scoped>
.tips {
  color: red;
  font-size: 11px;
  padding: 10px;
}
</style>



