<template>
  <div class="pay">
    <van-nav-bar title="" left-text="" left-arrow @click-left="$router.back()" />
    <van-row>
      <van-col span="24">
        <van-steps :active="step">
          <van-step></van-step>
          <van-step></van-step>
          <van-step></van-step>
        </van-steps>
      </van-col>
    </van-row>
    <van-row :gutter="20" style="margin-top: 20px;">
      <van-col :span="24" class="bill">
        
        <div>300.00</div>
        <img :src="img" alt="" style="width:100%;">
      </van-col>
      <van-col :span="24">
        <van-button type="primary" bottom-action class="btn" @click="handlePay"></van-button>
      </van-col>
    </van-row>
  </div>
</template>

<script>
import Img from '@/assets/imgs/pay.png'
export default {
  data() {
    return {
      img: Img,
      step: 2,
      list: [
        {
          name: '49',
          id: '1'
        },
        {
          name: '69',
          id: '2'
        }
      ],
      checked: ''
    }
  },
  methods: {
    handlePay() {
      this.$dialog
        .confirm({
          title: '',
          message: '300'
        })
        .then(() => {
          this.$toast.success('')
          this.$router.push({ name: 'PaySuccess' })
        })
        .catch(() => {
          this.$toast.fail('')
        })
    }
  }
}
</script>


<style lang="scss" scoped>
.pay {
  text-align: center;
}

.bill {
  color: red;
  font-size: 20px;
  div {
    color: red;
  }
}
</style>




